import * as colors from "@ant-design/colors";
import { TwitterPicker } from "react-color";

export default function ColorPicker(props) {
  return (
    <TwitterPicker
      {...props}
      styles={{
        default: {
          card: {
            boxShadow: "none",
            padding: 0,
            backgroundColor: "transparent",
          },
          body: { padding: 0 },
          hash: { height: 16, width: 16 },
          // input: { height: 16, width: 60, boxSizing: "border-box" },
          // swatch: { height: 24, width: 24, margin: "0px 6px 0px 0px" },
        },
      }}
      triangle="hide"
      width="100%"
      colors={[
        ...colors.red,
        ...colors.volcano,
        ...colors.gold,
        ...colors.yellow,
        ...colors.lime,
        ...colors.green,
        ...colors.cyan,
        ...colors.blue,
        ...colors.geekblue,
        ...colors.purple,
        ...colors.magenta,
        ...colors.grey,
      ]}
    />
  );
}
